.light{
    position: absolute;
    background-color: $primary;
    &-lg{
        width: 177px;
        height: 177px;
    }
    &-md{
        width: 147px;
        height: 147px;
    }
    &-sm{
        width: 113px;
        height: 113px;
    }
    &::before{
        content:'';
        position: absolute;
        border-radius: 50%;
        width: inherit;
        height: inherit;
    }
    &-start-bottom::before{
        bottom: -27px;
        left: -50%;
    }
    &-start-top::before{
        top: -27px;
        left: -50%;
    }
    &-end-bottom::before{
        bottom: -27px;
        right: -50%;
    }
}