html, body {
  background-color: #272620;
  font-family: 'Open Sans', sans-serif;
}

.text-hollow {
  color: transparent;
  -webkit-text-stroke: 1px #FFF8D4;
}

.text-border-2 {
  -webkit-text-stroke: 2px #FFF8D4;
}

.text-border-3 {
  -webkit-text-stroke: 3px #FFF8D4;
}

.hover {
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
}

.hover-link:hover {
  background-color: #FFF0A4 !important;
  color: #272620 !important;
}

.hover-link-reverse:hover {
  background-color: #272620 !important;
  color: #FFF8D4 !important;
}

.light {
  position: absolute;
  background-color: #FFF8D4;
}

.light-lg {
  width: 177px;
  height: 177px;
}

.light-md {
  width: 147px;
  height: 147px;
}

.light-sm {
  width: 113px;
  height: 113px;
}

.light::before {
  content: '';
  position: absolute;
  border-radius: 50%;
  width: inherit;
  height: inherit;
}

.light-start-bottom::before {
  bottom: -27px;
  left: -50%;
}

.light-start-top::before {
  top: -27px;
  left: -50%;
}

.light-end-bottom::before {
  bottom: -27px;
  right: -50%;
}

.title {
  margin-bottom: 183px;
}

.title-fs {
  font-size: 180px;
  font-weight: 600;
}

.title-fs-sub {
  font-size: 100px;
}

.title-hover {
  position: relative;
}

.title-hover span {
  -webkit-transition: all ease 0.5s;
  transition: all ease 0.5s;
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.title-hover-main span {
  top: -18px;
  left: -26px;
}

.title-hover-sub span {
  top: -12px;
  left: -29px;
}

.title-hover:hover > span {
  opacity: 1;
  visibility: visible;
}
